.featured{
    /* todo o tamanho do monito do usuario */
    height: 100vh;
}

.featured--vertical{
    /* vai herdar o tamanho do anterior */
    width: inherit;
    height: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);
}

.featured--horizontal{
    /* vai herdar o tamanho do anterior */
    width: inherit;
    height: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);

    /* colocar texto no meio */
    display:flex;
    flex-direction: column;
    justify-content: center;
    padding-left:30px;
    /* diminuir por causa de subir as listas */
    padding-bottom: 150px;
    /* diminuir por conta do cabeçalho */
    padding-top: 70px;

}
.featured--name{
    font-size:60px;
    font-weight:bold;
}
.featured--info{
    font-size:18px;
    font-weight:bold;
    margin-top: 15px;
}

.featured--points, .featured--year, .featured--seasons{
    display: inline-block;
    margin-right: 15px;
}


.featured--points{
    color: #46d369;    
}

.featured--description{
    margin-top: 15px;
    font-size: 20px;
    color:#999;
    max-width: 40%;
}

.featured--buttons{
    margin-top: 15px;

}

.featured--watchbutton,
.featured--mylistbutton{
    display: inline-block;
    font-size:20px;
    font-weight:bold;
    padding: 12px 25px;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;
    opacity: 1;
    transition: all 0.2s;
}

.featured--watchbutton:hover,
.featured--mylistbutton:hover{
    opacity: 0.7;
}

.featured--watchbutton{
    background-color: #fff;
    color: #000;
}

.featured--mylistbutton{
    background-color: #333;
    color: #fff;
}

.featured--genres{
    margin-top: 15px;
    font-size: 18px;
    color: #999;
}


@media (max-width:760px){
    .featured{
        height:90vh;
    }
    .featured--name{
        font-size: 40px;
    }
    .featured--info{
        font-size:16px;
    }
    .featured--description{
        font-size:14px;
        max-width: 100%;
        margin-right: 30px;
    }
    .featured--watchbutton,
    .featured--mylistbutton{
        font-size:16px;
    }
    .featured--genres{
        font-size:14px;
    }
}
